/* Visualizer  */

#zoom-out-editor-icon-container {
    position: absolute;
    z-index: 99999; 
    top: 48%;
    left: 48%;
    background-color: rgb(50, 62, 95);
    border-radius: 50%;
    padding: 5px;
    -webkit-transform: scale(0);
            transform: scale(0);
    transition: -webkit-transform 0.5s ease 0s;
    transition: transform 0.5s ease 0s;
    transition: transform 0.5s ease 0s, -webkit-transform 0.5s ease 0s;
}

#zoom-out-editor-icon-container.show {
    position: absolute;
    z-index: 99999; 
    top: 45%;
    left: 45%;
    background-color: rgb(50, 62, 95);
    border-radius: 50%;
    padding: 5px;
    -webkit-transform: scale(1);
            transform: scale(1);
    transition: -webkit-transform 0.5s ease 0s;
    transition: transform 0.5s ease 0s;
    transition: transform 0.5s ease 0s, -webkit-transform 0.5s ease 0s;

}

#zoom-out-editor-icon {
    display: none;
    font-size: 100%;
    color: white;
}

#zoom-out-editor-icon.show {
    display: block;
    font-size: 300%;
}
